import { AgGridVue } from "ag-grid-vue3";
import axios from "axios";
import ProgressCell from "@/components/ProgressCell.vue";
export default {
  components: {
    AgGridVue,
    ProgressCell
  },
  data() {
    return {
      searchType: "m.mat_code",
      // 검색 조건 
      searchText: "",
      // 검색어
      //메인그리드
      rowData: [],
      //메인그리드 필드
      columnDefs: [{
        field: "mat_code",
        headerName: "자재코드",
        flex: 2
      }, {
        field: "mat_name",
        headerName: "자재명",
        flex: 3
      }, {
        field: "store_quantity",
        headerName: "재고량",
        flex: 2,
        valueFormatter: params => {
          return params.value != null ? `${params.value}개` : '';
        }
      }, {
        field: "safe_inventory",
        headerName: "안전재고량",
        flex: 2,
        valueFormatter: params => {
          return params.value != null ? `${params.value}개` : '';
        }
      }, {
        field: "require_quantity",
        headerName: "필요량",
        flex: 2,
        valueFormatter: params => {
          return params.value != 0 ? `-${params.value}개` : 0;
        }
      }, {
        field: "percentage",
        headerName: "안전 대비 보유율 (%)",
        flex: 3,
        cellRenderer: 'ProgressCell'
      }],
      //상세그리드
      secondRowData: [],
      //상세그리드 필드
      secondColumnDefs: [
      // 체크박스 추가
      {
        field: "mat_name",
        headerName: "자재명",
        flex: 2
      }, {
        field: "lot",
        headerName: "LOT",
        flex: 2
      }, {
        field: "store_quantity",
        headerName: "재고량",
        flex: 1,
        valueFormatter: params => {
          return params.value != null ? `${params.value}개` : '';
        }
      }, {
        field: "store_name",
        headerName: "창고",
        flex: 1
      }],
      gridOptions: {
        domLayout: "autoHeight",
        singleClickEdit: true,
        suppressRowClickSelection: true,
        pagination: true,
        paginationPageSize: 10,
        paginationPageSizeSelector: false,
        overlayNoRowsTemplate: '표시할 값이 없습니다.',
        defaultColDef: {
          suppressMovable: true,
          resizable: false,
          sortable: false,
          cellStyle: {
            textAlign: "center"
          }
        }
      }
    };
  },
  mounted() {
    this.basicList();
  },
  methods: {
    async basicList() {
      await axios.get('/api/mat/matStorepage').then(res => {
        this.rowData = res.data;
      }).catch(err => console.log(err));
    },
    //검색 조회
    async searchMaterials() {
      await axios.get('/api/mat/matStorepage', {
        params: {
          type: this.searchType,
          keyword: this.searchText
        }
      }).then(res => {
        this.rowData = res.data;
      }).catch(error => {
        console.error(error);
      });
    },
    async matCellclicked(event) {
      await axios.get('/api/mat/matLotList/' + event.data.mat_code).then(res => {
        this.secondRowData = res.data;
      }).catch(err => console.log(err));
    }
  }
};